import { useCallback, useState } from 'react';
import { fetchMovie } from './api';
import './Movie.css';

function Movie (props) {
    const { movie: { Poster, Title, imdbID } } = props;
    const [isExpanded, setExpanded] = useState(false);
    const [movieDetails, setMovieDetails] = useState();
    const [isLoading, setLoading] = useState(false);

    const onMovieClick = useCallback(() => {
        const fetchMovieDetails = async () => {
            setLoading(true);
            const mov = await fetchMovie(imdbID);
            setLoading(false);
            setMovieDetails(mov);
        }

        if (!movieDetails && !isLoading) {
            fetchMovieDetails();
        }
        setExpanded(!isExpanded);

    }, [imdbID, isExpanded, isLoading, movieDetails, setExpanded, setMovieDetails, setLoading]);

    return (
        <div className="movie">
            <img className="movie-image" alt="" src={Poster === 'N/A' ? 'movie-placeholder.jpeg' : Poster} />
            <div className={ `footer ${isExpanded ? 'expanded': ''}` }>
                <div className="movie-footer">
                    <span>{Title}</span>
                    <button onClick={ onMovieClick } className="expand-button">{ isExpanded ? '-' : '+' }</button>
                </div>
                { isExpanded && isLoading && <div>Loading...</div> }
                {
                isExpanded && movieDetails && (
                    <div>
                        <div className="movie-detail"><b>Genre:</b> {movieDetails.Genre} </div>
                        <div className="movie-detail"><b>Director:</b> {movieDetails.Director} </div>
                        <div className="movie-detail"><b>Actors:</b> {movieDetails.Actors} </div>
                    </div>
                )}
            </div>
        </div>
    );
}

export default Movie;