const API_KEY = '1b704b01';

export async function fetchMovies(term) {
  return await fetch(
    `https://www.omdbapi.com/?s=${term}&apikey=${API_KEY}`,
  ).then(data => data.json());
}

export async function fetchMovie(movieId) {
  return await fetch(
    `https://www.omdbapi.com/?i=${movieId}&apikey=${API_KEY}`,
  ).then(data => data.json());
}

