import { useCallback, useState } from "react";
import { fetchMovies } from "./api";

function SearchBar (props) {
    const { onMoviesChange } = props;
    const [term, setTerm ] = useState('');

    const searchChange = useCallback((event) => {
        const { value } = event.target;
        setTerm(value);
    }, [setTerm]);

    const getMovies = useCallback(async () => {
        const { Search, Error } = await fetchMovies(term);
        onMoviesChange(Search, Error);
    }, [term, onMoviesChange]);

    return (
    <div className="search">
        <input type="text" value={ term } onChange={ searchChange } className="searchTerm" placeholder="What are you looking for?" />
        <button onClick={ getMovies } className="searchButton">
          <span className="f">Search</span>
       </button>
     </div>        
    )
}

export default SearchBar;